.fs2e.sheet.actor {

  /* Text */

  h1, h2, h3 {
    .p22();
    border: none;
    align-self: center;
  }


  /* Header */

  img {
    .shading();
    width: 100%;
    height: 100%;
    border: 1px solid var(--color-gold-dk-mid) !important;
  }

  .sheetname {
    padding-bottom: 5px;
    color: var(--color-gold-lt);

    input[type="text"] {
      font-size: var(--font-size-40);
      color: var(--color-gold-lt);
    }
  }

  .field,
  input[type="text"] {
    color: var(--color-gold-lt);

    .label {
      .p22();
      color: var(--color-gold-mid);
    }

    .placeholder {
      font-size: var(--font-size-12);
      color: var(--color-gold-dk-mid);
    }
  }

  .data-pill {
    padding-top: 5px;
  }


  .container {
    padding: 6px;
    color: var(--color-gold-lt);

    &.shade {
       .shading();
    }
  }

  /* Charactertistics aand Skills */

  .characteristics {
      text-align: center;

    h3 {
      background-color: var(--color-gold-dk);
    }

    .fs2e-grid-chars-spirit {
      grid-template-columns: 1fr auto 1fr;
    }

    .stat { 
      display: flex; 
      justify-content: space-between; 
      align-items: center;
      text-align: left;

      .spirit-right .stat .value {
        margin-right: auto;
        text-align: left;
      }

      .spirit-left .stat .value {
        text-align: right;
      }
    }

    .spirit .stat.sep { 
      display: flex; 
      justify-content: center; 
    }
    .spirit .stat.sep .sep { 
      display: inline-block; 
      width: 1ch;
      text-align: center; 
      min-width: 0;
    }



  }

  /* Sheet-specific grids */

  .fs2e-grid-sheet-header {
    display: grid;
    grid-template-columns: 160px 1fr;
    gap: 12px;
  }

  .fs2e-grid-sheet-fields {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 8px;
  }

  .fs2e-grid-sheet-age {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 12px;
  }

  .fs2e-grid-chars {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 12px;
  }

  .fs2e-grid-chars-body,
  .fs2e-grid-chars-mind,
  .fs2e-grid-chars-spirit-left,
  .fs2e-grid-chars-spirit-mid,
  .fs2e-grid-chars-spirit-right {
    display: grid;
    grid-template-columns: 1fr;
    gap: 8px;
  }

  .fs2e-grid-chars-spirit {
    display: grid;
    grid-template-columns: 1fr auto 1fr;
    gap: 12px;
  }

  .skills {
    h3 {
      background-color: var(--color-gold-dk);
      text-align: center;
    }
  }

  .skills .stat {
    display: flex;
    justify-content: space-between;
    align-items: center;
    text-align: left;
  }

  .skills .stat .value {
    margin-left: auto;
    min-width: 2ch;
    text-align: right;
  }

  .skills-add {
    padding: 0;
    border: 0;
    background: transparent;
    color: inherit;
    font: inherit;
    font-size: 1em;
    line-height: 1em;
    width: 1ch;
    height: 1em;
    display: inline-block;
    text-align: center;
    cursor: pointer;
  }

  .skills-remove {
    padding: 0;
    border: 0;
    background: transparent;
    color: inherit;
    font: inherit;
    font-size: 1em;
    line-height: 1;
    cursor: pointer;
  }

  .skills-group {
    display: flex;
    align-items: center;
    gap: 6px;
  }

  .skills-name-actions {
    display: inline-flex;
    align-items: center;
    gap: 6px;
  }

  .skills-subskill .skills-name-actions {
    gap: 0;
  }

  .skills-group-block,
  .skills-item {
    break-inside: avoid;
    margin-bottom: var(--gap-y, var(--grid-gap, 8px));
  }

  .skills-subskill {
    padding-left: 0;
    margin-top: var(--gap-y, var(--grid-gap, 8px));
  }

  .skills-remove-prefix {
    display: inline-block;
    width: 2ch;
    text-align: left;
    margin-right: 0.5ch;
  }

  .skills-subskill .name {
    width: 100%;
    background: transparent;
    border: 0;
    color: var(--color-gold-lt);
    font: inherit;
    line-height: 1em;
    height: 1em;
    padding: 0;
    margin: 0;
  }

  .fs2e-grid-skills-learned {
    display: block;
    column-count: 3;
    column-gap: var(--gap-x, var(--grid-gap, 8px));
  }

  .natural-list {
    display: block;
    column-count: 3;
    column-gap: 16px;
  }

  .natural-list .stat {
    break-inside: avoid;
    margin-bottom: 8px;
  }

  .learned-skills {
      padding: 10px 0 0 0;
  }
}
